
import React, { Component, Suspense } from 'react';
import Swal from 'sweetalert2';
import { connect } from 'react-redux';
import Networks from './Elements/Networks';



const NeedSupport = React.lazy(() => import('../../../CommonComponents/NeedSupport'));
const AddFeedByNetwork = React.lazy(() => import('./Elements/AddFeedByNetwork'));
const ChooseCollection = React.lazy(() => import('./Elements/ChooseCollection'));
const AssetsUploader = React.lazy(() => import('./Elements/AssetsUploader'));
const OnsiteUpload = React.lazy(() => import('../../../Feeds/OnsiteUpload/OnsiteUpload'));


class UploadAssets extends Component {

    constructor() {
        super();
        this.state = {
            uploadTypeSec: 'uploadAssets',
            filterTabs: [],
            selectedNetwork: null,
        };
    }

    uploadType = (uploadTypeVal, network) => {
        this.setState({
            uploadTypeSec: uploadTypeVal,
            filterTabs: network ? network.networkTab : null,
            selectedNetwork: network
        })
    }
    getAssets = () => {
        this.props.getAssetsCallback()
    };
    onUppyResetAndClose = event => {
        const { uploadedData } = this.props
        if (uploadedData && uploadedData.length > 0) {
            const { onClearUPloadObject, uppyPostEdit } = this.props
            uppyPostEdit.cancelAll()
            uppyPostEdit.removeFile()
            onClearUPloadObject([])
        } else {
            const { onClearUPloadObject, uppyPostEdit } = this.props
            uppyPostEdit.cancelAll()
            uppyPostEdit.removeFile()
            onClearUPloadObject([])
            this.props.editMediaHandleCallback(false)
        }

    }
    onCancelUpload = event => {
        const { uploadedData } = this.props
        var self = this;
        if (uploadedData && uploadedData.length > 0) {
            Swal.fire({
                title: 'Are you sure?',
                text: "You want to  discard changes!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: 'Yes, discard changes!'
            }).then((result) => {
                if (result.isConfirmed) {
                    const { onClearUPloadObject, uppyPostEdit } = self.props
                    uppyPostEdit.cancelAll()
                    uppyPostEdit.removeFile()
                    onClearUPloadObject([])
                }
            })
        }
        else {
            this.props.editMediaHandleCallback(false)
        }
    }

    render() {
        const { uploadTypeSec, filterTabs, selectedNetwork } = this.state;
        const { uploadedData, collectionError, networkList } = this.props

        return <div className="container container-xs">
            <div className="block-center flex-column view-sm-h">
                <div className="addSource d-flex align-items-stretch flex-column flex-lg-row w-100">
                    <div className="addSourceNetwork d-flex flex-column flex-shrink-0 pe-lg-4">
                        <div className="card mb-xxl-4 mb-3 h-100">
                            <div className="card-body px-2">
                                <Networks uploadTypeCallback={this.uploadType} networkList={networkList} selectedNetworkId={selectedNetwork ? selectedNetwork.id : null} />
                            </div>
                        </div>
                    </div>
                    <div className="d-flex flex-column flex-grow-1 w-100 mb-3">
                        <div className="">
                            <h6 className="fs-7 mb-3">Select the collection where you want to put your data</h6>
                            <div className="mb-3" style={{ maxWidth: 300 }}>
                                <Suspense fallback={<></>}>
                                    <ChooseCollection {...this.props} />
                                </Suspense>
                                {collectionError ? <span className="text-danger">Please select collection.</span> : null}
                            </div>
                            {uploadTypeSec === 'uploadAssets' ?
                                <Suspense fallback={<></>}>
                                    <AssetsUploader {...this.props} uppyPostEdit={this.props.uppyPostEdit} />
                                </Suspense>
                                : uploadTypeSec === 'onsite-upload' ?
                                    <Suspense fallback={<></>}>
                                        <OnsiteUpload onBack={() => this.uploadType("uploadAssets", null)} {...this.props} />
                                    </Suspense>
                                    : uploadTypeSec === 'byNetwork' ?
                                        <Suspense fallback={<></>}>
                                            <AddFeedByNetwork filterTabs={filterTabs} selectedNetwork={selectedNetwork} onBack={() => this.uploadType("uploadAssets", null)} {...this.props} />
                                        </Suspense>
                                        : null
                            }
                        </div>
                        {uploadTypeSec === 'uploadAssets' ? <div className="d-flex flex-wrap align-items-center justify-content-between">
                            <Suspense fallback={<></>}>
                                <NeedSupport content={'Need Help'} supportUrl={'#'} videoUrl={'#'} changeText={true} />
                            </Suspense>
                            <div className="my-1 d-flex align-items-center justify-content-end flex-wrap">
                                <button type="button" className="btn btn-secondary me-3" onClick={this.onCancelUpload}>Cancel</button>
                                <button type="button" id="resetUppyUpload" className="btn btn-secondary me-3" onClick={this.onUppyResetAndClose} hidden>Uppy Reset</button>
                                <button type="button" className={`btn btn-primary ${uploadedData && uploadedData.length > 0 ? `` : `disabled`}`} onClick={() => this.getAssets()}>Fetch Assets {uploadedData && uploadedData.length > 0 ? `(${uploadedData.length})` : ``}</button>
                            </div>
                        </div> : null}
                    </div>
                </div>
            </div>
        </div>
    }
}
const mapStateToProps = (state) => {
    return {
        networkList: state.addFeed.networkList
    }
}

export default connect(mapStateToProps)(UploadAssets);
